import i18n from '@/i18n'
import { store } from './store.js'

export const functions = {

	/**
	 * Il y a peut-tre mieux, mais permet de grer les boutons prcdents / suivants du navigateur
	 * ---
	 * On fait une mise  jour du hash dans le navigateur
	 * Cela dclenche l'vnement "hashchange" du navigateur
	 * Qui est dfini dans App.vue et qui dclenche changeStepFromHash ci-dessous
	 * ---
	 * TODO : vrifier en conditions relles
	 * @param {String} newStep
	 */
	 changeStep(newStep) {
		window.location.hash = newStep
	},
	/**
	 * Modification relle du "step" en cours
	 * @param {String} newStep
	 */
	changeStepFromHash(newStep) {
		store.state.step = newStep
		window.scrollTo(0, 0)
	},
	/**
	 * Gre la redirection  la fin du parcours
	 */
	quitAndRedirect() {
		if ( store.props.redirectUrl !== '' && store.props.redirectUrl !== undefined ){
			window.location = store.props.redirectUrl
		} else {
			window.location = this.getAccountUrl()
		}
	},
	/**
	 * Retourne l'adresse de Mon Compte
	 */
	getAccountUrl() {
		// TODO : rcuprer home_url ??
		if ( i18n.locale == 'fr' ){
			if (process.env.NODE_ENV === 'development') {
				return 'http://wedogood.local/mon-compte/'
			} else {
				return 'https://www.wedogood.co/mon-compte/'
			}
		} else {
			if (process.env.NODE_ENV === 'development') {
				return 'http://wedogood.local/my-account/'
			} else {
				return 'https://www.wedogood.co/my-account/'
			}
		}
	}

}